@media only screen and (max-width: 800px) {
    .topic_video_contents {
      margin-bottom: 1rem;
      position: relative;
      /* z-index: 1; */
    }
    .topic_thumnail_ {
      width: 100%;
      cursor: pointer;
    }
    .topic_titles_views_avatar {
      display: flex;
    }
    .ds_avatar_img {
      width: 3rem;
      margin-top: -1px;
    }
    .ds_title_views {
      margin-left: -0.1rem;
      font-size: 12px;
    }
    .ds_name {
      font-weight: bold;
      color: white
    }
    .ds_views {
      color: #aaaaaa;
    }
   
    .ds_artistname{
      display:none;
    }
  }
  @media only screen and (min-width: 800px) {
    .topic_video_contents {
      display: flex;
      width: 70%;
      margin: auto;
      margin-left: 20rem;
      position: relative;
      margin-bottom: 1rem;
      overflow: hidden;
    }
    .topic_thumbnail_div{
      overflow: hidden;
      width:60%;
      max-width: 400px;
    }
    .topic_titles_views_avatar {
      width: 40%; 
      max-width: 300px;
  }
    .topic_thumnail_ {
      width: 100%;
      max-width: 400px;
      cursor: pointer;
    }
    .ds_name {
      font-weight: 600;
      color: white;
  }
    .ds_views {
      color: #aaaaaa;
    }
    .ds_avatar_img {
      width: 2.7rem;
      bottom: 0;
    }
    .ds_name_div {
      margin-left: 1rem;
    }
    .topic_views_div {
      margin-left: 1rem;
      margin-top: 1rem;
    }
    .topic_artist_div {
      margin-left: 4rem;
      margin-top: 2.5rem;
    }
    .ds_artist {
      font-weight: 800;
      font-size: 12px;
    }
  .ds_artistname{
    font-weight: 800;
    font-size: 12px;
    margin-top:1.2rem;
    color: white;
  }
    .topic_thumnail_:hover {
      transform: scale(1.3);
      transition: transform ease 0.6s;
    }
    .ds_avatar{
      position: absolute;
      bottom: 0;
      margin-bottom: 3rem;
      margin-left: 1rem;
      display: flex;
    }
    .ds_artist{
      display:none
    }
    
  }
  @media only screen and (min-width: 992px) {
    .topic_artist_div {
      margin-left: 4rem;
      margin-top: 3.6rem;
    }
  }
.ds_artistname{
  color: white;;
}
.ds_name {
  font-weight: 600;
  color: white;
}